@import '../base.scss';

.c-active-ingredients {
  &__inner {
    margin-top: 100px;
    text-align: center;
    max-width: 125rem;
  }

  &__heading {
    margin-bottom: 38px;

    &.is-desktop {
      display: none;
    }
  }

  &__pretitle {
    margin-bottom: 6px;
  }

  &__right {
    position: relative;

    &:hover {

      .c-active-ingredients__play-button {
        transform: translate(-50%, -50%) scale(1);
      }
    }
  }

  &__images-wrapper{
    position: relative;
  }

  &__circle {
    width: 228px;
    height: 228px;
    border-radius: 50%;
    background-color: $s-color--background-tertiary;
    overflow: hidden;

    &-wrapper {
      @include t-absolute-center-x-y();

    }
  }

  &__play-button {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(.9);
    @include t-width-and-height(73);
    display: block;
    margin: 0 auto;
    transition: transform $s-anim--time-short $s-anim--ease-in-out;

    .icon--play {
      &-inner {
        fill: $s-color--background-tertiary;
      }
    }
  }

  //ingredients slider
  &__items {
    padding: 52px 0 7rem;
    overflow-x: auto;

    &.is-initialised {
      overflow: hidden;

      .c-active-ingredients__item {
        &.is-active {
          overflow: visible;

          .c-active-ingredients__item-icon {
            width: 80px;
            height: 80px;
            border-width: 2px;
            border-color: $s-color--background-septenary;
            background: $s-color--aqua;
            padding: 17px;
            margin: 0 auto auto;
            overflow: visible;


            svg,
            * {
              fill: $s-color--white;
            }
          }

          .c-active-ingredients__item-name {
            padding-top: 33px;
            color: $s-color--blue-green;
          }

          .c-active-ingredients__item-description {
            display: block;
            overflow: visible;


          }
        }


      }
    }
  }

  &__item {
    cursor: pointer;

    &-icon {
      padding: 10px;
      width: 47px;
      height: 47px;
      border: 1px solid $s-color--border-secondary;
      border-radius: 50%;
      margin: 16px auto auto;
      transition: all .5s;

      svg,
      * {
        transition: all .5s;
        fill: $s-color--dark-blue;
        width: 100%;
        height: auto;
        display: block;
      }
    }

    &-name {
      transition: all 0.3s ease;
      display: block;
      padding-top: 23px;
      color: $s-color--grey-green;
    }

    &-description {
      display: none;
      display: none;
      width: 70vw;
      transform: translateX(-30%);
      position: relative;
      top: 55px;

      p {
        line-height: 18px;
      }
    }
  }


  @include t-media-query($s-small-down) {
    &__image-container {
      padding-bottom: 81.5%;
    }
  }

  @include t-media-query($s-small-up) {

    &__image-container {
      padding-bottom: 54%;
    }

  }

  @include t-media-query($s-medium-up) {

    &__inner {
      @include t-gutters;
    }

    &__item {
      overflow: visible;

      &:nth-child(2) {
        .c-active-ingredients__item-description {
          transform: translateX(-20%);
        }
      }

      &:nth-child(3) {
        .c-active-ingredients__item-description {
          transform: translateX(-40%);
        }
      }

      &:nth-child(4) {
        .c-active-ingredients__item-description {
          transform: translateX(-60%);
        }
      }

      &:nth-child(5) {
        .c-active-ingredients__item-description {
          transform: translateX(-80%);
        }
      }

      &:hover {
        .c-active-ingredients__item-icon {
          border-color: $s-color--light-blue;
          background: $s-color--aqua;
        }

        .c-active-ingredients__item-name {
          color: $s-color--font-1;
        }

        svg,
        * {
          fill: $s-color--white;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      &-icon {
        padding: 10px;
        width: 56px;
        height: 56px;
        margin: 0 auto;

        .is-large & {
          width: 56px;
          height: 56px;
          border-width: 2px;
          border-color: $s-color--light-blue;
          background: $s-color--aqua;
          padding: 11px;

          svg,
          * {
            fill: $s-color--white;
          }
        }
      }

      &-name {
        padding-top: 28px;

        .is-large & {
          color: $s-color--font-1;
        }
      }

      &-description {
        padding-top: 35px;
        display: none;

        .is-large & {
          display: block;
          width: 500%;
          transform: translateX(0);
        }

        p {
          line-height: 18px;
        }
      }
    }

  }

  @include t-media-query($s-large-up) {
    &__inner {
      display: flex;
      text-align: left;
      flex-direction: row-reverse;
      justify-content: flex-start;
    }

    &__heading {
      margin-bottom: 38px;

      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__pretitle {
      margin-bottom: 25px;
    }

    &__right {
      width: 46.7%;
    }

    &__image-container {
      padding-bottom: 104%;
    }

    &__play-button {
      width: 110px;
      height: 110px;
    }

    &__left {
      margin-right: 100px;
    }

    &__items {
      text-align: left;
      padding-top: 27px;
    }

    &__item {
      width: 110px;
      margin-right: 32px;

      &:nth-child(2) {
        .c-active-ingredients__item-description {
          transform: translateX(-26%);
        }
      }

      &:nth-child(3) {
        .c-active-ingredients__item-description {
          transform: translateX(-51%);
        }
      }

      &:nth-child(4) {
        .c-active-ingredients__item-description {
          transform: translateX(-77%);
        }
      }

      &:nth-child(5) {
        .c-active-ingredients__item-description {
          transform: translateX(-102%);
        }
      }



      &-icon {
        margin: 0;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__circle {
      width: 499px;
      height: 499px;
    }

    &__left {
      margin-top: 194px;
      margin-right: 228px;
    }

    &__right {
      width: 45.5%;
    }
  }
}